.logo {
  float: right;
  width: 70px;
  height: 60px;
  margin-right: -30px;
}

.logoHeader{
  padding-top: -10px;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  -moz-border-radius: 50%;
  -webkit-border-radius: 50%;
  -o-border-radius: 50%;
 
}
a {
  color: inherit;
}
.item1 {
  border: none;
  background-color: inherit;
  color: #001529;
  padding-left: 10px;
  font-size: 20px;
  display: flex;
  justify-content: center;
  align-items:center;
  font-family: 'Almarai', sans-serif;
  background: #cdd2d6;
}
.item1:hover {
  text-decoration: none;
  color: #ee2260;
}
.item1:focus {
  outline: 0;
}
.item2 {
  background: #cdd2d6;
  color: #001529;
  margin: 1px;
  font-size: 17px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: 'Tajawal', sans-serif;
}


.item4 {
  font-size: 20px;
  background: #cdd2d6;
  display: flex;
  color:#001529;
  justify-content: center;
  align-items: center;
}
.item4:hover {
  background-color: #cdd2d6;
  transition: 0.4s;
}



.footer-container {
  color: aliceblue;
  text-align: center;
  background-color: #cdd2d6;
  font-size: 15px;
  width: 100%;
  height: 4rem;
  display: grid;
  grid-template-columns: 1fr 4fr 0.5fr  ;
}

/* Extra small devices (phones, 600px and down) */
@media only screen and (max-width: 600px) {
  .item1 {
      font-size: 12px;
      padding-left: 3px;
  }
  .item2 {
      font-size: 7px;
  }
  .item4
 {
      font-size: 12px;
}
}

/* Small devices (portrait tablets and large phones, 600px and up) */
@media only screen and (min-width: 600px) {
  .item1 {
      font-size: 14px;
      padding-left: 5px;
  }
  .item2 {
      font-size: 9px;
  }
  .item3,
  .item4,
  .item5 {
      font-size: 14px;
  }
}

/* Medium devices (landscape tablets, 768px and up) */
@media only screen and (min-width: 768px) {
  .item1 {
      font-size: 16px;
      padding-left: 6px;
  }
  .item2 {
      font-size: 11px;
  }
  .item3,
  .item4,
  .item5 {
      font-size: 16px;
  }
}

/* Large devices (laptops/desktops, 992px and up) */
@media only screen and (min-width: 992px) {
  .item1 {
      font-size: 18px;
      padding-left: 8px;
  }
  .item2 {
      font-size: 13px;
  }
  .item3,
  .item4,
  .item5 {
      font-size: 18px;
  }
}

/* Extra large devices (large laptops and desktops, 1200px and up) */
@media only screen and (min-width: 1200px) {
  .item1 {
      font-size: 20px;
      padding-left: 10px;
  }
  .item2 {
      font-size: 15px;
  }
  .item3,
  .item4,
  .item5 {
      font-size: 20px;
  }
}
